import './App.css';
import Card from './components/Card';
import AddCard from './components/AddCard';
import Footer from './components/Footer';
import User from './components/User';
import { useState } from 'react';

function App() {
  const [storedData, setSoredData] = useState(localStorage.getItem('websiteData'));
  const [submit, setSubmit] = useState(false);
  const [del, setdel] = useState(false);
  const [user, setUser] = useState(localStorage.getItem('imuser'));
  //localStorage.removeItem('websiteData');
  const refresh = () => {
    console.log('refreshed');
    setSoredData(localStorage.getItem('websiteData'));
  }
  const defaultData = [
    {
        "name": "Chat GPT",
        "url": "https://chatgpt.com/",
        "iconUrl": "https://chatgpt.com//favicon.ico",
        "color": "#1e1e1e"
    }
  ];
  const data = storedData ? JSON.parse(storedData) : defaultData;

  return (
    <div className="App">
      {data.map((item) => (
        <Card 
          key={item.url} 
          name={item.name} 
          url={item.url} 
          iconUrl={item.iconUrl}
          color={item.color}
          del={del}
          data={data}
          carddeleted={del}
          refresh={refresh}
        />
      ))}
      <AddCard data={data} refresh={refresh} />
      <Footer
        setv={setSubmit} 
        del={setdel} 
        refresh={refresh} 
        user={user} />
      <User 
        setSubmit={setSubmit}
        submit={submit}
        setUser={setUser}
      />
    </div>
  );
}

export default App;
